import './index.less';
import {Component} from 'san';
import '../../assets/swiper.css';
import template from './index.html';
import Swiper from '@/lib/utils/swiper';
export default class Actor extends Component {
    static template = template;
    async attached() {
        new Swiper(this.el, {
            freeMode: true,
            slidesPerView: 'auto',
            setWrapperSize: true,
        });
    }
    authorClick(index, item) {
        const {auhomepageurl} = item;
        window.location = auhomepageurl;
    }
    followClick(index, item) {
        this.fire('followClick', {index, item});
    }
    initData() {
        return {
            focus: {}
        };
    }
}