/**
 * 获取变量类型
 *
 * @param {*} value 待验变量
 * @return {string} 类型字符串
 */
export function getVariableType(value) {
    var typeString = Object.prototype.toString.call(value);
    var match = typeString.match(/\[object ([A-z]+)\]/);
    return match ? match[1].toLowerCase() : '';
}
