/**
 * 新闻栏目页面
 * @file yangmei(yangmei03@baidu.com)
 */
import '../store';
import './app.less';
import {Component} from 'san';
import {connect} from '@store';
import ubc from '@baidu/ubc-report-sdk';
import {env} from '@baidu/auc-base/env';
import concern from '@baidu/xbox-concern';
import {order} from '../service';
import versionCompare from '@baidu/xbox/version_compare';
import {getQueryString} from '@/lib/utils/getQueryString';
import {setShareInfo} from '../../../lib/utils/setShareInfo';
// components
import TopCarousels from '../components/top-carousels';
import MiddleOrder from '../components/middle-order';
import MainIntroduce from '../components/main-introduction';
import Actor from '../components/actor-intro';
import GuideBottom from '../components/guide-bottom';
import boxx from '@baidu/auc-base/boxx';

// 尝试关闭加载态
try {
    env.isBoxSeries && boxx.call('commonUI.closeLoading');
} catch (e) {
    console.error(e);
}

export default class App extends Component {
    constructor(opt) {
        super(opt);
    }
    static template = `
        <div class="wrapper" style="">
            <div class="top-wrapper"></div>
            <div class="maincontent-wrapper">
                <ui-carousels majorPrograms="{{majorPrograms}}" on-clickInvoke="clickInvoke"/>
                <ui-order order="{{order}}" on-orderClick="orderClick"/>
                <ui-actor focus="{{focus}}" on-followClick="followClick"/>
                <ui-introduction introduce="{{introduce}}"/>
            </div>
            <div class="bottom-wrapper">
                <ui-guide-bottom on-clickInvoke="clickInvoke" jumpChannel="{{jumpChannel}}"/>
            </div>
        </div>
    `;
    static components = {
        'ui-carousels': TopCarousels,
        'ui-order': MiddleOrder,
        'ui-introduction': MainIntroduce,
        'ui-actor': Actor,
        'ui-guide-bottom': GuideBottom
    };
    attached() {
        this.initConcern();
        // 页面打点
        ubc('pageLog.event', {
            type: 'pageShow',
            ext: {
            }
        });

        setShareInfo({
            title: '高燃来袭！百度自制烧脑综艺上线',
            description: '在《决胜21天》中看懂博弈思维',
            icon: 'https://b.bdstatic.com/searchbox/icms/searchbox/img/8003-logo.jpg',
            link: window.location.href
        });
    }
    initData() {
        return {
            initConcern: undefined

        };
    }
    initLog() {
        // 页面打点
        ubc('pageLog.init', '13908', {
            from: getQueryString('id') || '',
            source: getQueryString('source') || 'other'
        });
    }
    inited() {
        this.initLog();
    }
    clickInvoke(urlCmd) {
        this.actions.invoke({urlCmd});
    }
    initConcern() {
        const initConcern = concern({
            id: 'news-notice',
            ops: {
                type: 'stock',
                isAladin: 0,
                onLine: true
            }
        });
        this.data.set('initConcern', initConcern);
    }
    followClick({index, item}) {
        const initConcern = this.data.get('initConcern');
        initConcern.subscribe({
            type: item.followtype,
            op_type: item.followed ? 'cancel' : 'add',
            third_id: item.aumrid,
            sfrom: 'sbox',
            source: item.followtype
        }, () => {
            this.actions.updateFocusFollow({
                index
            });
        }, (err) => {
            console.error(JSON.stringify(err));
        }, false);
    }
    orderClick() {
        const pageData = this.data.get('pageData');
        const orderData = this.data.get('order');
        const orderStatus = orderData.subscribe_state;
        if (!env.isBoxSeries) {
            this.clickInvoke(pageData.target);
            return;
        }
        if (!pageData.is_login) {
            if (versionCompare(env.boxVersion, '12.2') < 0) {
                boxx.call('commonUI.showToast', {
                    type: '1',
                    message: '登陆后才可预约，版本过低当前页面无法登陆'
                });
                return;
            }
            boxx.call('account.login', {
                loginType: 'username',
                showThirdLogin: '1',
                loginSource: 'pay_landing',
                normalizeAccount: '1',
                success: data => {
                    location.reload();
                    pageData.is_login = 1;
                }
            });
        } else {
            order({
                paUid: orderData.pa_uid,
                topicId: orderData.topic_id,
                subscribe: orderStatus ? 0 : 1
            }).then(({errno, data}) => {
                if (+errno) {
                    boxx.call('commonUI.showToast', {
                        type: '1',
                        message: '操作失败，请重试'
                    });
                    return;
                }
                // 更新预约状态
                this.actions.updateOrder({
                    data: {
                        subscribe_state: orderStatus ? 0 : 1,
                        real_num: data.order_num
                    }
                });
                // 预约提示toast
                const message = !orderStatus
                    ? '更新后订阅消息提醒已开启' : '已取消预约';
                boxx.call('commonUI.showToast', {
                    type: '1',
                    message,
                });
            });
        }
    }
}
connect.san({
    pageData: 'pageData',
    order: 'order',
    focus: 'focus',
    majorPrograms: 'majorPrograms',
    jumpChannel: 'jumpChannel',
    introduce: 'introduce'
}, {
    updateFocusFollow: 'main:updateFocusFollow',
    invoke: 'invoke',
    updateOrder: 'main:updateOrder'
})(App);