import axios from 'axios';
import qs from 'qs';
import {getUrlParam} from '../../../lib/utils';

const parseJSON = result => {
    const data = result.data;
    if (typeof data === 'string') {
        try {
            return JSON.parse(data);
        } catch (e) {
            return data;
        }
    } else {
        return data;
    }
};

// 获取页面数据common接口
export const getCommonData = () => axios.get(
    `/newspage/data/lorepage?id=${getUrlParam('id')}&format=json`).then(parseJSON);

export const order = params => axios.post(
    '/newspage/data/loresubscirbe', qs.stringify({
        subscribe: params.subscribe,
        pa_uid: params.paUid,
        topic_id: params.topicId
    })
).then(parseJSON);