import {store} from '@store';
import {builder} from 'san-update';
import {invokeApp} from '@baidu/ug-invoke-app';
import invoke from '@baidu/xbox/na/invoke';
import {env} from '../../../lib/utils/env';

export const main = {
    initData: {
        env,
        pageData: undefined,
        order: undefined,
        focus: undefined,
        majorPrograms: undefined,
        jumpChannel: undefined,
        introduce: undefined
    },
    actions: {
        ['main:updatePageInfo'](data, {dispatch, getState}) {
            const {
                modules
            } = data;
            if (modules.length) {
                modules.map(item => {
                    dispatch(`main:${item.type}Data`, item);
                });
            }
            dispatch('main:updatePageData', data);
        },
        ['main:orderData'](data) {
            return builder().set('order', data);
        },
        ['main:focusData'](data) {
            return builder().set('focus', data);
        },
        ['main:major_programsData'](data) {
            return builder().set('majorPrograms', data);
        },
        ['main:jump_channelData'](data) {
            return builder().set('jumpChannel', data);

        },
        ['main:introduceData'](data) {
            return builder().set('introduce', data);
        },
        ['main:updatePageData'](data) {
            return builder().set('pageData', data);
        },
        ['main:updateFocusFollow']({index}, {getState}) {
            const focus = getState('focus');
            const followData = focus.data;
            const oldData = followData[index];
            followData[index] = Object.assign({}, oldData, {
                followed: oldData.followed ? 0 : 1
            });
            return builder().set('focus.data', {...followData});
        },
        ['main:updateOrder']({data} = {}, {getState}) {
            const result = {
                ...getState('order'),
                ...data
            };
            return builder().set('order', {...result});
        },
        ['invoke']({urlCmd}, {getState}) {
            const {isBDBox} = getState('env');
            if (isBDBox) {
                invoke(urlCmd);
            } else {
                invokeApp({
                    appName: 'baiduboxapp',
                    scheme: urlCmd
                });
            }
        }
    }
};

store.initData(main.initData).addActions(main.actions);