import App from '@/lib/App';
import {store} from '@store';
import AppComponent from './containers/app';
import {getCommonData} from './service/index';

const initData = result => {
    // 将页面数据存储进store中
    store.dispatch('main:updatePageInfo', result);
    App(AppComponent, '#app');
};

(() => {
    if (window.sortsData) {
        initData(window.sortsData);
    } else {
        getCommonData().then(({data, errno}) => {
            if (+errno === 0 && data) {
                initData(data);
                return;
            }
            initData({
                error: true
            });
        });
    }
})();